@import '../../theme/colors.scss';

.content {
  margin-bottom: 50px;
  .discussions_container {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    background-color: color(white);
    align-items: center;
  }

  .pagination {
    display: flex;
    list-style: none;
    cursor: pointer;
    padding-inline-start: 0px;
    justify-content: center;
    margin-top: 0px;

    @media screen and (max-width: 600px) {
      width: 100%;
      margin: 0px 15px 0px 15px;
    }
  }

  .hide_border {
    a {
      border: none !important;
      font-size: 20px !important;
      color: color(lightGrey) !important;
    }
  }

  .pagination a {
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    border-radius: 20px;
    border: 1px solid color(lightGrey);
    color: color(black);
    margin: 0px 4px;
    font-size: 14px;

    @media screen and (max-width: 600px) {
      width: 20px;
      height: 20px;
      border-radius: 10px;
      font-size: 12px;
      padding-inline-start: 0px;
    }
  }

  .pagination__link {
    font-weight: bold;
    width: 40px;
    height: 40px;
    border-radius: 20px;
  }

  .pagination__link__active a {
    color: #fff;
    background: color(green);
  }

  .pagination__lin__disabled a {
    color: color(grey);
    border: 1px solid color(grey);
  }
}
